#header {
    height: 70px;
    z-index: 1000;
    padding: 8px 0;
    border-bottom: 1px solid #ffb600;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: white;
}

.amac-nav-logo {
    width: 47px;
}

.amac-nav-logo-mobile {
    width: 45%;
    margin: 0 auto 30px;
}

#amac-nav li {
    list-style: none;
}

#amac-nav .main-nav:not(:first-child) {
    margin-left: 20px;
}

#amac-nav .main-nav a:not(.amac-nav-btn) {
    padding: 6px 10px;
    text-decoration: none;
}

#amac-nav li a:not(.amac-nav-btn):hover,
#amac-nav li a:not(.amac-nav-btn):focus {
    color: #c83e38 !important;
}

.amac-nav-btn {
    line-height: 1em;
}

.amac-nav-btn-mobile {
    font-size: 1em !important;
}

.amac-link {
    font-size: 1.2em !important;
}

.amac-link-mobile {
    font-size: 0.9em !important;
}
#amac-banner {
  padding: 70px 0;
}

.banner-sub {
  margin-bottom: 2.3rem;
}

.banner-sponsor-1-img-wrap,
.banner-sponsor-2-img-wrap {
  width: 200px;
}
#amac-about {
    background-color: #F9FBFD;
    padding: 5em 0;
}

.about-desc {
    width: 85%;
}

.about-img {
    border-radius: 50px 0 50px 0;
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.4);
    -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.4);
    -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.4);
}

.about-img img {
    border-radius: 50px 0 50px 0;
}

.about-numbers {
    margin-top: 8em;
}
#past-amac {
    background-color: #fcf3f2;
    padding: 5em 0;
}

.past-amac-thumbnail {
    width: 100%;
    overflow: hidden;
}

.past-amac-thumbnail img {
    width:100%;
    height:100%;
    object-fit: cover;
}

.past-amac-thumbnail img {
    transition: -webkit-transform 0.4s;
    transition: transform 0.4s;
    transition: transform 0.4s, -webkit-transform 0.4s;
}
  
.past-amac-thumbnail:hover img {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
#events {
    background-color: #F9FBFD;
    padding: 5em 0;
}

.event-item {
    position: relative;
}

.events-thumbnail {
    width: 100%;
    overflow: hidden;
    border-radius: 30px;
}

.events-thumbnail img {
    width:100%;
    height:100%;
    object-fit: cover;
}

.events-thumbnail img {
    transition: -webkit-transform 0.4s;
    transition: transform 0.4s;
    transition: transform 0.4s, -webkit-transform 0.4s;
}
  
.events-thumbnail:hover img {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}

.event-content {
    position: absolute;
    width: 100%;
    bottom: 0;
}

.event-item-title {
    padding-left: 0.7em;
}
/* Tickets */
.form-check-input:checked {
    background-color: #c83e38 !important;
    border-color: #c83e38 !important;
}

.ticket-cancel-btn {
    border: 1px solid #888888;
}

.ticket-cancel-btn:hover,
.ticket-cancel-btn:focus {
    border-color: #c83e38;
    color: #c83e38;
}

/* AMAC Video */
.past-amac-video video {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.video-close-btn {
    position: absolute;
    top: -50px;
    right: 0px;
    background-color: #ff7158;
    border-radius: 50%;
    font-weight: bold !important;
}

.video-close-btn:hover,
.video-close-btn:focus {
    background-color: #ff6047;
}

