@media (max-width: 400px) {
    /* .past-amac-thumbnail, */
    .past-amac-video {
        height: 200px;
    }
}

/* Speakers */
.no-rounded {
    border-radius: 0.3rem 0.3rem 0 0 !important;
}

.speakers-det-thumbnail {
    width: 200px;
    height: 200px;
    overflow: hidden;
}

.speakers-det-thumbnail img {
    width:100%;
    height:100%;
    object-fit: cover;
}

.speakers-det-thumbnail img {
    transition: -webkit-transform 0.4s;
    transition: transform 0.4s;
    transition: transform 0.4s, -webkit-transform 0.4s;
}
  
.speakers-det-thumbnail:hover img {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
#speakers {
    padding: 5em 0;
}

.speaker-item {
    border-radius: 15px;
}

.speaker-cover-photo {
    width: 100%;
    padding: 1em 0;
    border-radius: 15px 15px 0 0;
}

.speaker-dp {
    width: 50%;
    border: 3px solid white;
}

.speaker-dp, .speaker-dp img {
    border-radius: 50%;
}

.speaker-content {
    background-color: #f7f7f7;
    border-radius: 0 0 15px 15px;
}

.speaker-btn {
    font-size: 1em !important;
}

.speaker-job {
    height: 50px;
}

.speaker-desc {
    height: 130px;
}
#tickets {
    padding: 5em 0;
    background: rgb(255,182,0);
    background: linear-gradient(39deg, rgba(255,182,0,1) 0%, rgba(255,113,88,1) 100%);
}

.ticket-title {
    margin-bottom: 1em;
}

.ticket-img {
    margin-bottom: 1.1em;
}

.ticket-range-wrap {
    width: 300px;
    margin: 0 auto 0.6em;
}

.ticket-range-outer,
.ticket-range-inner {
    height: 10px;
}

.ticket-range-outer {
    width: 100%;
    background: #ddd;
}

.ticket-range-inner {
    background: rgb(255,113,88);
    background: linear-gradient(95deg, rgba(255,113,88,1) 0%, rgba(200,62,56,1) 100%);
}

.ticket-inclusion-item {
    width: 50%;
}
#apply-merchant {
    padding: 5em 0;
    background-attachment: fixed;
}

.merchant-sub {
    width: 85%;
}

.merchant-img {
    border-radius: 50px 0 50px 0;
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.4);
    -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.4);
    -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.4);
}

.merchant-img img {
    border-radius: 50px 0 50px 0;
}
#partners {
    padding: 5em 0;
    background-color: #F9FBFD;
}

.sponsor-item {
    width: 300px;
}

.sponsor-item-sm {
    width: 150px;
}

.sponsor-item-md {
    width: 200px;
}

.sponsors-section {
    margin: 2.5em 0;
}


#apply-sponsor {
    padding: 5em 0;
    background-attachment: fixed;
}

.apply-sponsor-img {
    border-radius: 50px 0 50px 0;
    box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.4);
    -webkit-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.4);
    -moz-box-shadow: 0px 0px 10px 1px rgba(0,0,0,0.4);
}

.apply-sponsor-img img {
    border-radius: 50px 0 50px 0;
}
#faq {
    padding: 5em 0;
    background: rgb(187,235,214);
    background: linear-gradient(48deg, rgba(187,235,214,1) 0%, rgba(255,113,88,1) 100%);
}

/* content */
.accordion {
    margin-top: 3em;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
}

.accordion-button {
    font-size: 0.8em !important;
    letter-spacing: normal;
    font-weight: normal;
}

.accordion-button:not(.collapsed) {
    color: #fff !important;
    background-color: transparent !important;
    border-bottom: 1px solid whitesmoke;
    box-shadow: none !important;
}

.accordion-button:focus {
    border-color: white !important;
    box-shadow: none !important;
}

.accordion-item,
.accordion-button {
    background-color: transparent !important;
    color: #fff;
}

.accordion-item {
    border-bottom-color: white;
}

.accordion-button::after {
    display: none;
}
#amac-footer {
    padding: 3em 0;
}

.footer-icon-link {
    width: 40px;
    height: 40px;
    border: 1px solid #777;
    border-radius: 50%;    
    padding: 7px;
    transition: all 0.5s ease;
}

.footer-icon-link:hover,
.footer-icon-link:focus {
    background-color: #ff7158;
    border-color: #ff7158;
}
#art-schedule {
    padding: 5em 0;
    background: rgb(255,182,0);
    background: linear-gradient(39deg, rgba(255,182,0,1) 0%, rgba(255,113,88,1) 100%);
}

.art-sections {
    padding: 5em 0;
    height: 70vh;
}

.tpt-wrap {
    position: absolute;
    width: 300px;
    bottom: 0;
    right: 0;
}

.tpt-logo {
    width: 80px;
}
